@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap);
.cartScreen{
    max-width: 1300px;
    margin: 2rem auto;
    padding: 15px;
    display: -webkit-flex;
    display: flex;
}

.cart__left{
    -webkit-flex: 0.7 1;
            flex: 0.7 1;
    padding: 8px;
    margin-right: 20px;
}
.cart__right{
    -webkit-flex: 0.3 1;
            flex: 0.3 1;
    box-shadow: 0 8px 10px rgba(1, 1, 1, 0.6);
    padding: 8px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #efefef;
    color: #1b1b1b;
}

.cart__button__checkout{
    width: 100%;
    padding: 10px 0;
    font-size: 1.2rem;
    border: 1px solid teal;
    color: teal;
    cursor: pointer;
    margin: 20px 0;
}
.cart__button__checkout:hover{
    border: 1px solid tomato;
    color: white;
    background-color: tomato;

}
.cart__info > h3{
    margin-bottom: 20px ;
}

@media (max-width: 890px){
    .cartScreen{
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .cart__left{
        margin-right: 0;
    }
    .cart__right{
        margin-top: 3rem;
    }
    
}
.cartItem {
  width: 100%;
  padding: 1rem;
  margin-bottom: 10px;
  display: grid;
  grid-template-columns: 2fr 2fr 1fr 1fr 1fr;
  grid-gap: 8px;
  gap: 8px;
  place-items: center;
  margin-bottom: 15px;
  box-shadow: 0 10px 10px rgba(1, 1, 1, 0.6);
  background-color: #efefef;
  color: #1b1b1b;
}
.cartItem__name {
  text-decoration: none;
  color: black;
  font-size: 2rem;
}

.cartItem__name:hover {
  color: tomato;
}

.cartItem__select {
  padding: 10px 40px;
  font-size: 1.2rem;
}
.cart__delete {
  color: red;
  font-size: 1.7rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: 0.7;
}
.cart__delete:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@media (max-width: 700px) {
  .cartItem__name {
    font-size: 1.2rem;
  }
  .cartItem__select {
    padding: 5px 10px;
  }
  .cart__price{
      font-size: 0.7rem;
  }
}
@media (max-width: 360px) {
  .cartItem {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom: 20px;
  }
  .cartItem::after{
      content: "";
      width: 100%;
      height: 0.5px;
      background-color: lightgray;
  }
  .cartItem__select {
   padding: 5px 50px;

  }
}

