.navbar{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 30px 20px;
    box-shadow: 0 0 30px 0 lightgray;
    z-index: 10;
    background-color: #efefef;
}
.nav__logo{
    font-size: 2rem;
    cursor: pointer;
    color: teal;
}
.nav__links{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    list-style-type: none;
    padding: 0;
}
.nav__links li{
    padding: 0 10px;
}
.nav__links li a{
   text-decoration: none;
   color: teal;
   font-weight: 700;
   font-size: 1.5rem;
}
.nav__links li span{
    background-color: red;
    border-radius: 50%;
    color: white;
    padding: 0 5px;
    font-size: 1.1rem;
}
.nav__cart{
    padding: 5px 10px;
    transition: .5s;
}
.nav__cart:hover{
    background-color: #efefef;
    border-radius: 20px;
}

.nav__hamburger{
    width: 30px;
    height: 30px;
    display: none;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    cursor: pointer;

}
.nav__hamburger > div{
    width: 100%;
    height: 6px;
    border-radius: 2px;
    background-color: teal;
}

@media (max-width: 760px){
    .nav__links{
        display: none;
    }
    .nav__hamburger{
        display: -webkit-flex;
        display: flex;
    }
}
@media (max-width: 361px){
    .nav__logo{
        font-size: 1.2rem;
    }
}
.backdrop{
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 11;
    position: fixed;
    top: 0;
    left: 0;
}
.sidedrawer {
  width: 70%;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  background-color: white;
  z-index: 12;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all 0.3s ease-in-out;
}

.sidedrawer.show {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.sidedrawer__links {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  list-style-type: none;
}
.sidedrawer__links > li {
  padding: 10px 0;

}

.sidedrawer__links a {
  text-decoration: none;
  font-size: 2rem;
  color: teal;
}
.sidedrawer__cart{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.sidedrawer__cart--badge {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 30px;
  height: 30px;
  font-size: 1rem;
  border-radius: 50%;
  background-color: red;
  color: white;
}

